<template>
    <div class="page">
        <div class="banner">
            <vue-particles
                    color="#fff"
                    :particleOpacity="0.7"
                    :particlesNumber="60"
                    shapeType="polygon"
                    :particleSize="4"
                    linesColor="#fff"
                    :linesWidth="1"
                    :lineLinked="true"
                    :lineOpacity="0.4"
                    :linesDistance="150"
                    :moveSpeed="2"
                    :hoverEffect="true"
                    hoverMode="grab"
                    :clickEffect="true"
                    clickMode="push"
                    class="back"
            >
            </vue-particles>
            <div class="word">
                <p class="info">解决方案</p>
                <p style="font-size: 20px;margin-top: 35px;">产业园区 "智慧资管·智慧物联·智慧服务·大数据可视" 全场景解决方案</p>
            </div>
        </div>
        <div class="bread">
            <div class="target">
                <router-link to="/solve/contain1">方案1</router-link>
                <router-link to="/solve/contain2">方案2</router-link>
                <router-link to="/solve/contain3">方案3</router-link>
            </div>
            <div class="bread-list">
                <i class="fa fa-map-marker"></i>
                <el-breadcrumb separator-class="el-icon-arrow-right" class="bread-crumb">
                    <el-breadcrumb-item v-for="item in breadList" :to="{path:item.path}" :key="item.name">
                        {{item.meta.title}}
                    </el-breadcrumb-item>
                </el-breadcrumb>
            </div>
        </div>
        <router-view></router-view>
    </div>
</template>

<script>
// @ is an alias to /src

export default {
    name: "solve",
    data() {
        return {
            breadList: []
        };
    },
    created () {
        this.getBreadcrumb()
        this.$store.commit('nowPage', 3)
        localStorage.nowPage = 3;
    },
    watch: {
        $route () {
            this.getBreadcrumb();
        }
    },
    methods: {
        isHome(route) {
            return route.name === "home";
        },
        getBreadcrumb () {
            let matched = this.$route.matched;
            //如果不是首页
            if (!this.isHome(matched[0])) {
                matched = [{ path: "/", meta: { title: "首页" } }].concat(matched);
            }
            this.breadList = matched;
        }
    }
};
</script>

<style lang="less" scoped>
    .bread-list{
        margin-right: 5%;
        height: 60px;
        float: right;
        i{
            font-size: 24px;
        }
        .bread-crumb{
            display: inline-block;
        }
    }
</style>
