<template>
    <div class="contain">
        <div class="abstractlist">
            <a class="border" style="display: block;border:1px solid #0369fc;color:#0369fc;">找到我们</a>
            <a @click="show()">联系我们</a>
        </div>
        <ul>
            <li>
                <baidu-map :center="center" :zoom="zoom" :disableDoubleClickZoom="true"
                           :dragging="true" @ready="handler" class="map" ak="1FtoPznuBDPQen7ZCZ0HjApYCUvrfApp"><!--显示地图-->
                    <bm-marker :position="center" :icon="{url: MapIcon, size: {width: 30, height: 45}}"
                               animation="BMAP_ANIMATION_BOUNCE">
                        <bm-info-window :show=true style="width: 300px">
                            <div class="infobox" >
                                <div class="infotop">
                                    <p>西湖风景区</p>
                                    <a href="http://api.map.baidu.com/place/detail?uid=df95f91c9c62cbb438221c0e&amp;output=html&amp;source=jsapi&amp;operate=mapclick&amp;clicktype=tile" target="_blank">在百度地图中打开»</a>
                                </div>
                                <div class="infobottom">
                                    <p>地址：浙江省杭州市西湖区龙井路1号</p>
                                    <p style="color: #7f7f7f;">标签：写字楼 房地产 大厦</p>
                                </div>
                            </div>
                        </bm-info-window>
                    </bm-marker><!--显示中心动画-->
                    <bm-scale anchor="BMAP_ANCHOR_BOTTOM_RIGHT"></bm-scale><!--比例尺-->
                    <bm-navigation anchor="BMAP_ANCHOR_TOP_RIGHT"></bm-navigation><!--缩放控件-->
                    <bm-map-type :map-types="['BMAP_NORMAL_MAP', 'BMAP_HYBRID_MAP']" anchor="BMAP_ANCHOR_TOP_LEFT"></bm-map-type><!--卫星影像-->
                </baidu-map>
            </li>
        </ul>
        <el-dialog title="联系我们" :visible.sync="dialogFormVisible" custom-class="dialogstyle" id="dialog">
            <form>
                <p class="p1">
                    <input type="text"  id="name" autocomplete='off' name="name" placeholder="* 您的姓名：">
                    <input type="text" id="zhiwu" autocomplete='off' name="zhiwu" placeholder="您的职务：">
                    <input type="text" id="tel" autocomplete='off' name="tel" placeholder="* 联系电话：">
                    <input type="text" id="email" autocomplete='off' name="email" placeholder="联系邮箱：">
                </p>
                <p class="p2">
                    <select id="fangxiang" name="fangxiang">
                        <option value="售前咨询">* 售前咨询</option>
                        <option value="售后服务">* 售后服务</option>
                        <option value="技术合作">* 技术合作</option>
                        <option value="产学研合作">* 产学研合作</option>
                        <option value="渠道合作">* 渠道合作</option>
                        <option value="我是供应商">* 我是供应商</option>
                        <option value="新闻媒体">* 新闻媒体</option>
                        <option value="其他">* 其他</option>
                    </select>
                    <input type="text" id="company" name="company" placeholder="* 公司全称：">
                    <input type="text" id="city" name="city" placeholder="* 所在城市：">
                </p>
                <textarea id="remark" autocomplete='off' name="remark" placeholder="意向简述："></textarea>
                <div class="p3">
                    <div class="ident">
                        <input type="text" autocomplete="off" required="required"  placeholder="验证码" name="captcha" id="captcha">
                        <div class="code" @click="refreshCode">
                            <Identify :identifyCode="identifyCode"></Identify>
                        </div>
                    </div>
                    <input type="submit" @click="check" value="提交" class="button">
                    <input type="reset" @click="show()" value="取消" class="button">
                </div>
            </form>
        </el-dialog>
    </div>
</template>

<script>
    import Identify from "../identify";
    export default {
        data() {
            return {//经，纬
                center: {lng: 0, lat: 0},
                zoom: 1,
                pos: {lng: 0, lat: 0},
                identifyCodes: '1234567890',
                identifyCode: '',
                dialogFormVisible: false,
            };
        },
        components: {
            Identify
        },
        created() {
            this.MapIcon = require("../../assets/img/mark.png");
            this.refreshCode();
        },
        methods: {
            handler() {
                this.center.lng = 120.12792;
                this.center.lat = 30.228932;
                this.pos.lng = this.center.lng;
                this.pos.lat = this.center.lat;
                this.zoom = 18;
            },
            randomNum(min, max) {
                return Math.floor(Math.random() * (max - min) + min)
            },
            refreshCode() {
                this.identifyCode = ''
                this.makeCode(this.identifyCodes, 4)
            },
            makeCode(o, l) {
                for (let i = 0; i < l; i++) {
                    this.identifyCode += this.identifyCodes[
                        this.randomNum(0, this.identifyCodes.length)
                        ]
                }
            },
            check(){
                if(document.getElementById('captcha').value==this.identifyCode)
                    this.$message({
                        message: 'correct',
                        type: 'success',
                        offset:100
                    });
                else if(document.getElementById('captcha').value!='')
                    this.$message({
                        message: 'wrong',
                        type: 'error',
                        offset:100
                    });
            },
            show() {
                this.dialogFormVisible = !this.dialogFormVisible
            }
        },
        mounted() {
            this.identifyCode = "";
            this.makeCode(this.identifyCodes, 4);
        }
    }
</script>

<style lang="less" scoped>
    .contain {
        width: 90%;
        margin-left: 5%;
        background: white;
        .abstractlist {
            width: 90%;
            left: 5%;
            height: 40px;
            padding-top: 60px;
            position: relative;

            a {
                width: 12%;
                line-height: 35px;
                height: 35px;
                border-radius: 17.5px;
                color: #666;
                padding: 0;
                text-align: center;
                display: inline-block;
                font-size: 14px;
                position: relative;
                transition: all 0.5s;
                border: 1px transparent;
                float: left;
                left: 34%;
                &:hover {
                    border: 1px solid #0369fc;
                    color: #0369fc;
                }
            }
        }
        ul {
            height: 600px;
            display: block;
            li {
                padding-top: 2%;
                .map {
                    overflow: hidden;
                    height: 500px;
                    border: 2px solid #999;
                    cursor: pointer;
                    .infobox {
                        width: 100%;
                        height: 100%;

                        .infotop {
                            p {
                                font-weight: bold;
                                display: inline-block;
                                margin: 0;
                                color: #4d4d4d;
                            }

                            a {
                                margin-left: 10px;
                                font-size: 14px;
                                text-decoration: none;
                                color: #3d6dcc;
                            }
                        }

                        .infobottom {
                            margin-top: 5%;

                            p {
                                font-weight: normal;
                                font-size: 14px;
                                margin: 0;
                                color: #4d4d4d;
                            }
                        }
                    }
                }
            }
        }
    }
</style>

<style lang="less">
    .dialogstyle {
