            .p1 {
                width: 100%;
                height: 50px;

                input {
                    width: 20%;
                    height: 100%;
                    margin: 0 1%;
                    padding: 0 1%;
                    border-radius: 10px;
                    border: double #0c4793;
                }
            }

            .p2 {
                width: 100%;
                height: 50px;
                margin-top: 3%;

                select {
                    width: 29%;
