                    border-radius: 10px;
                    border: double #0c4793;
                }

                input {
                    width: 29%;
                    height: 100%;
                    margin: 0 1%;
                    padding: 0 1%;
                    border-radius: 10px;
                    border: double #0c4793;
                }
            }

            textarea {
                width: 92%;
                height: 100px;
                margin: 2% 2%;
                padding: 20px;
                border-radius: 10px;
                border: double #0c4793;
            }

            .p3 {
                width: 100%;
                height: 50px;

                .ident {
                    width: 22%;
