                    border: double #0c4793;
                    display: inline-block;
                    vertical-align: middle;

                    input {
                        width: 33%;
                        height: 100%;
                        line-height: 50px;
                        float: left;
                        border: 0;
                        font-size: 14px;
                        padding: 0 20px;
                        background: transparent;
                    }

                    .code {
                        height: 100%;
