                        cursor: pointer;
                    }
                }

                .button {
                    display: inline-block;
                    width: 8%;
                    height: 55px;
                    background: #0f71fc;
                    font-size: 14px;
                    color: #fff;
                    text-align: center;
                    line-height: 40px;
