<template>
    <div class="floor">
        <div class="info">
            <p>服务案例</p>
            <h3>深度服务全国20城，40+大型园区，2000+园内企业</h3>
        </div>
        <div class="piclist">
            <swiper :options="swiperOption">
                <swiper-slide v-for="(item,index) in imglist" :key="index" >
                    <div class="pic">
                        <div class="imgfather">
                            <img :src="item.url" >
                        </div>
                        <h3>杭州软件新城——世界IT创新服务基地</h3>
                        <p class="discribe">平台将产业园、商业、公共资源等多业态集成管理，提升效率<br/>
                        通过一站式服务体系，打造智慧园区靓丽名片，提升品牌效应<br/>
                            通过智能设备整合，降低能耗，减少运营成本，提升工作效率</p>
                        <p class="end"></p>
                    </div>
                </swiper-slide>
                <div class="swiper-button-prev" slot="button-prev"></div>
                <div class="swiper-button-next" slot="button-next"></div>
            </swiper>
        </div>
        <router-link to="/service/example" class="btn">
            <p class="more">查看更多</p>
        </router-link>
        <el-row :gutter="40" class="panel-group">
            <el-col v-for="item in 4" class="card-panel-col">
                <i class="el-icon-picture-outline-round card-panel-icon"></i>
                <div class="card-panel-description">
                    <div class="card-panel-text">
                        New Visits
                    </div>
                    <!--          自动计数效果，炫就完事-->
                    <count-to :startVal='0' :endVal='setEndtVal' :duration="5000" class="card-panel-num" />
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    import CountTo from 'vue-count-to'
    export default {
        name: "floor4",
        components: {
            CountTo
        },
        data() {
            return {
                imglist: [
                    {url: require('../../assets/img/binner.png')},
                    {url: require('../../assets/img/binner2.jpg')},
                    {url: require('../../assets/img/binner2.jpg')}
                ],
                visitdata: [{},{},{},{}],
                setEndtVal:0,
                swiperOption: {
                    direction: "horizontal",
                    observer: true,
                    observeParents: true,
                    slidesPerView: 3,
                    spaceBetween: 30,
                    autoplay: {
                        delay: 6000,
                        stopOnLastSlide: false,
                        disableOnInteraction: true,
                    },
                    centeredSlides: true,
                    loop:true,

                    navigation: {
                        nextEl: ".swiper-button-next",
                        prevEl: ".swiper-button-prev"
                    },
                }
            }
        },
        methods:{
            handleScroll(){
                this.scroll = document.documentElement.scrollTop + document.body.scrollTop; //获取滚动后的高度
                if( this.scroll >2500 ){  //判断滚动后高度超过2500px,就开始计数
                    this.setEndtVal=2020;
                    window.removeEventListener('scroll', this.handleScroll)
                }
            },
        },
        mounted() {
            window.addEventListener('scroll', this.handleScroll)
        },
        beforeDestroy() {
            window.removeEventListener('scroll', this.handleScroll)
        }
    }
</script>

<style lang="less" scoped>
    .info {
        font-size: 32px;
        color: #333;
        line-height: 32px;
        position: relative;
        padding-top:2%;
        height: 13%;

        p:after {
            content: '';
            width: 90px;
            height: 3px;
            background: #4b9bd4;
            position: absolute;
            margin-left: -7%;
            margin-top: 2.5%;
        }

        h3 {
            font-size: 16px;
            padding-top: 1%;
        }
    }
    .piclist{
        width: 90%;
        height: 65%;
        margin-left: 5%;
        position: relative;
        .swiper-container {
            height: 100%;
        }
    }
    .pic{
        height: 335px;
        overflow: hidden;
        transition: all 1s;
        color:black;
        .imgfather{
            overflow: hidden;
            width: 100%;
            height: 280px;
            img{
                width: 100%;
                height: 100%;
                transition: all 0.5s;
            }
        }
        &:hover{
            img{
                transform:translateZ(0) scale(1.1);
            }
            height: 480px;
        }
        h3{
            font-size: 20px;
            line-height: 55px;
        }
    }
    .discribe{
        font-size: 14px;
        line-height: 30px;
    }
    .end{
        content: '';
        width: 100%;
        height: 2px;
        background: #4b9bd4;
        position: relative;
        top: 1%;
    }

</style>

<style lang="less">
    .panel-group {
        width: 90%;
        margin-left: 5% !important;
        .card-panel-col {
            box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
            height: 80px;
            width: 20%;
            margin: 2% 2.5%;
            border-radius: 20px;

            .card-panel-icon {
                float: left;
                font-size: 48px;
                line-height: 80px;
            }

            .card-panel-description {
                height: 100%;

                .card-panel-text {
                    color: rgba(0, 0, 0, 0.45);
                    line-height: 30px;
                    font-size: 24px;
                    margin-bottom: 12px;
                }

                .card-panel-num {
                    font-size: 24px;
                }

            }
        }
    }
</style>