<template>
    <div class="page">
        <div class="banner">
            <vue-particles
                    color="#fff"
                    :particleOpacity="0.7"
                    :particlesNumber="60"
                    shapeType="triangle"
                    :particleSize="4"
                    linesColor="#fff"
                    :linesWidth="1"
                    :lineLinked="true"
                    :lineOpacity="0.4"
                    :linesDistance="150"
                    :moveSpeed="2"
                    :hoverEffect="true"
                    hoverMode="grab"
                    :clickEffect="true"
                    clickMode="push"
                    class="back"
            >
            </vue-particles>
            <div class="word">
                <p class="info">———赋能企业成长  助力产业发展———</p>
            </div>
        </div>
        <div class="bread">
            <div class="target">
                <router-link to="/aboutus/introduce">公司简介</router-link>
                <router-link to="/aboutus/news">新闻中心</router-link>
                <router-link to="/aboutus/map">联系我们</router-link>
            </div>
            <div class="bread-list">
                <i class="fa fa-map-marker"></i>
                <el-breadcrumb separator-class="el-icon-arrow-right" class="bread-crumb">
                    <el-breadcrumb-item v-for="item in breadList" :to="{path:item.path}" :key="item.name">
                        {{item.meta.title}}
                    </el-breadcrumb-item>
                </el-breadcrumb>
            </div>
        </div>
        <router-view></router-view>
    </div>
</template>

<script>
// @ is an alias to /src

export default {
    name: "demand",
    data() {
        return {
            breadList: []
        };
    },
    created () {
        this.getBreadcrumb();
        this.$store.commit('nowPage', 5)
        localStorage.nowPage = 5;
    },
    watch: {
        $route () {
            this.getBreadcrumb();
        }
    },
    methods: {
        isHome(route) {
            return route.name === "home";
        },
        getBreadcrumb () {
            let matched = this.$route.matched;
            //如果不是首页
            if (!this.isHome(matched[0])) {
                matched = [{ path: "/", meta: { title: "首页" } }].concat(matched);
            }
            this.breadList = matched;
        }
    }
};
</script>

<style lang="less" scoped>
    .word {
        left: 28% !important;
        .info {
            text-align: center;
        }

        p:after{
            width: 0 !important;
        }
    }


    .bread-list{
        margin-right: 5%;
        height: 60px;
        float: right;
        i{
            font-size: 24px;
        }
        .bread-crumb{
            display: inline-block;
        }
    }
</style>
