import Vue from 'vue'
import Router from 'vue-router'
import Home from "./views/HomePage";

import Demand from "./views/DemandPage";
import Demandcon1 from "./components/DemandScenes/contain1";
import Demandcon2 from "./components/DemandScenes/contain2";
import Demandcon3 from "./components/DemandScenes/contain3";

import Solve from "./views/SolvePage";
import Solvecon1 from "./components/SolveScenes/contain1";
import Solvecon2 from "./components/SolveScenes/contain2";
import Solvecon3 from "./components/SolveScenes/contain3";

import Service from "./views/ServicePage"
import Services from "./components/ServicePage/services"
import Example from "./components/ServicePage/example"

import Aboutus from "./views/AboutusPage"
import Introduce from "./components/AboutusScenes/introduce"
import News from "./components/AboutusScenes/news"
import Map from "./components/AboutusScenes/map"
import Newsdetail from"./components/AboutusScenes/newsdetail"

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    }
    return new Promise((resolve) => {
      setTimeout(() => {
        resolve({ x: 0, y: 1 });
      }, 0);
    });
  },
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
    },
    {
      path: '/demand',
      component: Demand,
      name:'demand',
      redirect: '/demand/contain1',
      meta:{  title:'场景需求'},
      children: [{
        path: '/demand/contain1',
        component: Demandcon1,
        name:'demandcon1',
        meta:{  title:'场景1'},
      },{
        path: '/demand/contain2',
        component: Demandcon2,
        name:'demandcon2',
        meta:{  title:'场景2'},
      },{
        path: '/demand/contain3',
        component: Demandcon3,
        name:'demandcon3',
        meta:{  title:'场景3'},
      }]
    },
    {
      path: '/solve',
      component: Solve,
      name:'solve',
      redirect: '/solve/contain1',
      meta:{  title:'解决方案'},
      children: [{
        path: '/solve/contain1',
        component: Solvecon1,
        name:'solvecon1',
        meta:{  title:'方案1'},
      },{
        path: '/solve/contain2',
        component: Solvecon2,
        name:'solvecon2',
        meta:{  title:'方案2'},
      },{
        path: '/solve/contain3',
        component: Solvecon3,
        name:'solvecon3',
        meta:{  title:'方案3'},
      }]
    },
    {
      path: '/service',
      component: Service,
      name:'service',
      redirect: '/service/services',
      meta:{  title:'客户服务'},
      children: [{
        path: '/service/services',
        component: Services,
        name:'services',
        meta:{  title:'服务体系'},
      },{
        path: '/service/example',
        component: Example,
        name:'example',
        meta:{  title:'客户案例'},
      }]
    },
    {
      path: '/aboutus',
      component: Aboutus,
      name:'aboutus',
      redirect: '/aboutus/introduce',
      meta:{  title:'关于我们'},
      children: [{
        path: '/aboutus/introduce',
        component: Introduce,
        name:'introduce',
        meta:{  title:'公司简介'},
      },{
        path: '/aboutus/news',
        component: News,
        name:'news',
        meta:{  title:'新闻中心'},
      },{
        path: '/aboutus/map',
        component: Map,
        name:'map',
        meta:{  title:'联系我们'},
      },{
        path: '/aboutus/newsdetail',
        component: Newsdetail,
        name: 'newsdetail',
        meta:{  title:'新闻详情'},
      }]
    }
  ]
})

Router.selfaddRoutes = function (params){
  Router.matcher = new Router().matcher;
  Router.addRoutes(params)
}