<template>
  <div id="app">
    <title>XXX</title>
    <headItem></headItem>
    <div class="main-box">
      <router-view></router-view>
    </div>

    <footItem></footItem>
    <div>
		<el-button @click="drawer = true" type="primary" class="ebutton">联</el-button>
		<el-drawer title="联系我们" style="z-index=-1"
				   :visible.sync="drawer"
				   :with-header="false"
				   :show-close="false">
		  <form class="appform">
			<input class="inputs" type="text"  id="name" autocomplete='off' name="name" placeholder="* 您的姓名：">
			<input class="inputs" type="text" id="zhiwu" autocomplete='off' name="zhiwu" placeholder="您的职务：">
			<input class="inputs" type="text" id="tel" autocomplete='off' name="tel" placeholder="* 联系电话：">
			<input class="inputs" type="text" id="email" autocomplete='off' name="email" placeholder="联系邮箱：">
			<select id="fangxiang" name="fangxiang">
			  <option value="售前咨询">* 售前咨询</option>
			  <option value="售后服务">* 售后服务</option>
			  <option value="技术合作">* 技术合作</option>
			  <option value="产学研合作">* 产学研合作</option>
			  <option value="渠道合作">* 渠道合作</option>
			  <option value="我是供应商">* 我是供应商</option>
			  <option value="新闻媒体">* 新闻媒体</option>
			  <option value="其他">* 其他</option>
			</select>
			<input class="inputs" type="text" id="company" name="company" placeholder="* 公司全称：">
			<input class="inputs" type="text" id="city" name="city" placeholder="* 所在城市：">

			<textarea id="remark" autocomplete='off' name="remark" placeholder="意向简述："></textarea>
			<div class="ident">
			  <input type="text" autocomplete="off" required="required"  placeholder="验证码" name="captcha" id="captcha">
			  <div class="code" @click="refreshCode">
				<Identify :identifyCode="identifyCode" :contentHeight="53"></Identify>
			  </div>
			</div>
			<div class="btns">
			  <input type="submit" @click="check" value="提交" class="button">
			  <input type="reset" @click="drawer = false" value="关闭" class="button">
			</div>
		  </form>
		</el-drawer>
	</div>

    <el-backtop :right=35></el-backtop>
  </div>
</template>

<script>
import headItem from "./views/HeadPage";
import footItem from "./views/FootPage"
import Identify from "./components/identify";

export default {
  name: "frame",
  data() {
    return {
      drawer: false,
      identifyCodes: '1234567890',
      identifyCode: ''
    };
  },
  components: {
    headItem,
    footItem,
    Identify
  },
  created () {
    document.title = 'XXX';
    this.refreshCode();
  },
  methods: {
    randomNum(min, max) {
      return Math.floor(Math.random() * (max - min) + min)
    },
    refreshCode() {
      this.identifyCode = ''
      this.makeCode(this.identifyCodes, 4)
    },
    makeCode(o, l) {
      for (let i = 0; i < l; i++) {
        this.identifyCode += this.identifyCodes[this.randomNum(0, this.identifyCodes.length)]
      }
    },
    check() {
      if (document.getElementById('captcha').value == this.identifyCode)
        this.$message({
          message: 'correct',
          type: 'success',
          offset: 100
        });
      else if (document.getElementById('captcha').value != '')
        this.$message({
          message: 'wrong',
          type: 'error',
          offset: 100
        });
    },
    handleScroll(){
      this.scroll = document.documentElement.scrollTop + document.body.scrollTop; //获取滚动后的高度
      if( this.scroll >3700 ){  //判断滚动后高度超过2500px,就开始计数
        this.drawer = true;
        window.removeEventListener('scroll', this.handleScroll)
      }
      // console.log(this.scroll)
    },
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll)
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll)
  }
};
</script>


<style lang="less">
  body {
    font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
    "Microsoft YaHei", "微软雅黑", Arial, sans-serif !important;
    overflow-y: visible;
    overflow-x: hidden;
    max-width: 1920px;
    width: 100%;
    margin: 0;
  }
  .ebutton{
    position: fixed;
    width: 40px;
    height: 40px;
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.12);
    z-index: 5;
    right: 35px;
    bottom: 90px;
    text-align: center !important;
    padding: 0!important;
  }
  .main-box{
	padding-bottom: 425px;
  }
  
  .appform {
    height: 80%;
    width: 90%;
    margin-left: 5%;
    z-index: 100;
    margin-top: 10%;

    .inputs {
      width: 40%;
      height: 50px;
      margin: 3% 3%;
      padding: 0 1%;
      border-radius: 10px;
      border: double #0c4793;
    }

    select {
      width: 93%;
      height: 56px;
      margin: 0 3%;
      padding: 0 1%;
      border-radius: 10px;
      border: double #0c4793;
    }

    textarea {
      width: 82%;
      height: 100px;
      margin: 1% 3%;
      padding: 20px;
      border-radius: 10px;
      border: double #0c4793;
    }

    .ident {
      width: 70%;
      height: 9%;
      border-radius: 10px;
      border: double #0c4793;
      display: inline-block;
      vertical-align: middle;
      margin: 3% 15%;
      input{
        width: 50%;
        height: 95%;
        border: none;
        margin-left: 2%;
      }
      .code {
        float: right;
        cursor: pointer;
      }
    }

    .btns {
      width: 100%;
      height: 50px;
      border-radius: 10px;

      .button {
        display: inline-block;
        width: 30%;
        height: 50px;
        background: #0f71fc;
        font-size: 15px;
        color: #fff;
        text-align: center;
        line-height: 40px;
        margin-left: 14%;
        cursor: pointer;
        transition: all 0.5s;
        border: none;
        border-radius: 10px;
      }
    }
  }
</style>

